"use client";

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { fetchAPI } from '@/lib/api';
import { Plus, Edit2, Trash2, ExternalLink } from 'lucide-react';

export default function AdminProjects() {
    const [projects, setProjects] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetchAPI('/projects').then((data) => {
            setProjects(data);
            setLoading(false);
        }).catch((err) => {
            console.error(err);
            setLoading(false);
        });
    }, []);

    const handleDelete = async (id: string) => {
        if (!confirm('Are you sure you want to delete this project?')) return;
        try {
            await fetchAPI(`/projects/${id}`, { method: 'DELETE' });
            setProjects(projects.filter(p => p.id !== id));
        } catch (error) {
            console.error('Failed to delete', error);
        }
    };

    return (
        <div>
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-black">Manage Portfolio</h1>
                <Link href="/admin/projects/new" className="flex items-center gap-2 px-6 py-3 bg-dark text-white rounded-lg font-bold hover:bg-black transition-colors">
                    <Plus size={20} />
                    Add Project
                </Link>
            </div>

            <div className="bg-dark border border-white/10 rounded-2xl overflow-hidden">
                <table className="w-full text-left">
                    <thead className="bg-white/5 border-b border-white/10">
                        <tr>
                            <th className="px-6 py-4 font-bold text-sm uppercase text-gray-400">Project Name</th>
                            <th className="px-6 py-4 font-bold text-sm uppercase text-gray-400">Role/Tags</th>
                            <th className="px-6 py-4 font-bold text-sm uppercase text-gray-400">Date</th>
                            <th className="px-6 py-4 font-bold text-sm uppercase text-gray-400 text-right">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-white/10">
                        {loading ? (
                            <tr>
                                <td colSpan={4} className="px-6 py-12 text-center text-gray-500">
                                    Loading projects...
                                </td>
                            </tr>
                        ) : projects.map((project) => (
                            <tr key={project.id} className="hover:bg-white/5 transition-colors">
                                <td className="px-6 py-4">
                                    <div className="font-bold text-white">{project.title}</div>
                                    <div className="text-xs text-gray-500 font-mono mt-1">{project.slug}</div>
                                </td>
                                <td className="px-6 py-4 text-sm text-gray-400">
                                    {project.tags?.join(', ') || project.role || '-'}
                                </td>
                                <td className="px-6 py-4 text-sm text-gray-400">
                                    {project.created_at ? new Date(project.created_at).toLocaleDateString() : '-'}
                                </td>
                                <td className="px-6 py-4 text-right">
                                    <div className="flex items-center justify-end gap-2">
                                        <Link href={`/admin/projects/${project.id}/edit`} className="p-2 text-gray-500 hover:text-primary transition-colors">
                                            <Edit2 size={18} />
                                        </Link>
                                        <button onClick={() => handleDelete(project.id)} className="p-2 text-gray-500 hover:text-red-500 transition-colors">
                                            <Trash2 size={18} />
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                        {!loading && projects.length === 0 && (
                            <tr>
                                <td colSpan={4} className="px-6 py-12 text-center text-gray-500">
                                    No projects found. Add your first project!
                                </td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
